<template>
  <div class="home">
    <div class="fixed-top" @click="handleCity">
      {{this.$store.state.city}}
    </div>
    <van-swipe :autoplay="3000" indicator-color="white">
      <van-swipe-item>
        <img :src="datas[0].images.medium" alt class="pic" />
      </van-swipe-item>
      <van-swipe-item>
        <img :src="datas[1].images.small" alt  class="pic" />
      </van-swipe-item>
    </van-swipe>
    <div class="nav-box">
      <router-link to="/nowPlaying">
        <p>正在热映</p>
      </router-link>
      <router-link to="/comingSoon">
        <p>即将上映</p>
      </router-link>
    </div>
    <div class="loging">
    <van-loading v-show="this.$store.state.isLoading" type="spinner" color="#1989fa" />

    </div>
    <keep-alive exclude="comingSoon,nowPlaying">
    <router-view></router-view>
    </keep-alive>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: "home",

  data() {
    return {
      datas: []
    };
  },
  mounted() {
    var url = "https://douban.uieee.com/v2/movie/in_theaters";
    this.axios.get(url).then(res => {
    
      var datas = res.data.subjects;
      

      this.datas = datas.slice(0, 2);
    });
  },
  methods:{
    handleCity(){
      this.$router.push({name:'city'})
    }
  }
};
</script>
